<template>
  <div>
    <div class="filters">
      <p>
        <label>ID</label>
        <input type="text" v-model='filters.id'>
      </p>
      <p>
        <label>NAME</label>
        <input type="text" v-model='filters.name'>
      </p>
      <p>
        <label>LOCATION</label>
        <input type="text" v-model='filters.location'>
      </p>
      <p>
        <label>CURRENCY</label>
        <input type="text" v-model='filters.currency'>
      </p>
    </div>
    <table>
      <thead>
        <tr>
          <th colspan="2">ID</th>
          <th 
            class="pointer"
            @click='sorters.sort = "name"; sorters.dir = !sorters.dir'
          >NAME <span>{{sorters.sort == 'name' ? (sorters.dir ? '+' : '-') : ''}}</span></th>
          <th 
            class="pointer"
            @click='sorters.sort = "location"; sorters.dir = !sorters.dir'
          >LOCATION <span>{{sorters.sort == 'location' ? (sorters.dir ? '+' : '-') : ''}}</span></th>
          <th 
            class="pointer"
            @click='sorters.sort = "currency"; sorters.dir = !sorters.dir'
          >CURRENCY <span>{{sorters.sort == 'currency'? (sorters.dir ? '+' : '-') : ''}}</span></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for='row in sortedFilteredData' :key='row.id'>
          <td><router-link :to="'/item/' + row.id">{{row.id}}</router-link></td>
          <td>
            <template v-if="editConfig.id == row.id">
              <button @click="editConfig.action">ok</button>
              <button @click="editConfig.cancel">cancel</button>
            </template>
            <button v-else @click="updateData(row)">edit</button>
          </td>
          <td
            :contenteditable="editConfig.id == row.id"
            :class="editConfig.id == row.id ? 'edit' : ''"
            :ref="editConfig.id == row.id ? 'name' : ''"
          >{{row.name}}</td>
          <td
            :contenteditable="editConfig.id == row.id"
            :class="editConfig.id == row.id ? 'edit' : ''"
            :ref="editConfig.id == row.id ? 'location' : ''"
          >{{row.location}}</td>
          <td
            :contenteditable="editConfig.id == row.id"
            :class="editConfig.id == row.id ? 'edit' : ''"
            :ref="editConfig.id == row.id ? 'currency' : ''"
          >{{row.currency}}</td>
        </tr>
        <tr>
          <td></td>
          <td></td>
          <td>Full</td>
          <td>{{currencySum}}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'

export default {
  data() {
    return {
      filters: {
        name: '',
        location: '',
        currency: '',
        id: ''
      },
      sorters: {
        sort: '',
        dir: ''
      },
      editConfig: {
        id: '',
        action: null,
        cancel: null
      }
    }
  },
  computed: {
    ...mapState(['storeData']),
    ...mapMutations(['update']),
    sortedFilteredData() {
      let list = [...this.storeData]

      if(this.filters.id) {
        list = list.filter(e => e.id.toLowerCase().includes(this.filters.id.toLowerCase()))
      }
      if(this.filters.name) {
        list = list.filter(e => e.name.toLowerCase().includes(this.filters.name.toLowerCase()))
      }
      if(this.filters.location) {
        list = list.filter(e => e.location.toLowerCase().includes(this.filters.location.toLowerCase()))
      }
      if(this.filters.currency) {
        list = list.filter(e => ('' + e.currency).includes(this.filters.currency))
      }

      if (this.sorters.sort) {
        console.log(1);
        
        list = list.sort((a,b) => {         
          if (a[this.sorters.sort] > b[this.sorters.sort]) return this.sorters.dir ? 1 : -1;
          if (a[this.sorters.sort] < b[this.sorters.sort]) return this.sorters.dir ? -1 : 1;
        })
      }

      return list;
    },
    currencySum() {
      if (!this.sortedFilteredData.length) return 0;
      return this.sortedFilteredData.map(e => +e.currency).reduce((sum, el) => sum + el)
    }
  },
  methods: {
    updateData(data) {
      this.editConfig.id = data.id;
      new Promise((res, rej) => {
        this.editConfig.action = res;
        this.editConfig.cancel = rej;
      }).then(e => {
          let name = this.$refs.name[0].textContent;
          let location = this.$refs.location[0].textContent;
          let currency = this.$refs.currency[0].textContent;
          
          if (!/^[A-Za-z]*$/.test(name)) {
            alert('Invalid name. Name should contain letters only');
            return this.editConfig.id = null;
          }
          if (!/^[A-Za-z]*$/.test(location)) {
            alert('Invalid location. Location should contain letters only');
            return this.editConfig.id = null;
          }
          if (!/^[0-9]*$/.test(currency)) {
            alert('Invalid currency. Currency should contain digits only');
            return this.editConfig.id = null;
          }      
          
          this.store.commit('update', {...data, name, location, currency});

      }).catch(e => {
        this.editConfig.id = null
      })
    }
  },
  mounted() {
    // console.log(this.test)
  }
}
</script>

<style>
.pointer {
  cursor: pointer;
}

table {
  border-collapse: collapse;
}

td, th {
  border: 1px solid;
}

.edit {
  box-shadow: inset 0 0 3px 0;
}
</style>