<template>
  <div>
    <table>
      <tr v-for="val, key in item" :key="val">
        <td>{{key}}</td>
        <td>{{val}}</td>
      </tr>
    </table>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'Item',
  computed: {
    ...mapState(['storeData']),
    item() {
      for (let i = 0; i < this.storeData.length; i++) {
        if (this.storeData[i].id == this.$route.params.id) {
          return this.storeData[i];
        }
      }      
    }
  }
}
</script>